.skeleton {
  background: #ddd;
  margin: 10px 0;
  border-radius: 4px;

  &.text{
    width: 100%;
    height: 12px;
    
  }
  &.title{
    width: 50%;
    height: 20px;
    margin-bottom: 15px;
  }
  &.avatar{
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  &.thumbnail{
    width: 100px;
    height: 100px;
  }
}

.skeleton-wrapper{
  margin: 20px auto;
  padding: 10px 15px;
  border-radius: 4px;
  position: relative;
  overflow: hidden;


  &.light {
    background: #f2f2f2;
  }
  &.dark {
    background: #444;
    .skeleton {
      background: #777;
    }
    .shimmer {
      background: rgba(255,255,255,0.05);
    }
  }
  
}

.skeleton-profile{
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 30px;
  align-items: center;
}

.shimmer-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: loading 1s infinite;
}

.shimmer{
  width: 50%;
  height: 100%;
  background: rgba(255,255,255,0.2);
  transform: skewX(-20deg);
  box-shadow: 0 0 30px 30px rgba(255,255,255, 0.05);
}

@keyframes loading {
  0% {transform: translateX(-150%)}
  50% {transform: translateX(-60%);}
  100% {transform: translateX(150%);}
}
